"use client";

import { useEffect, useRef } from "react";
import { Chart } from "chart.js";
import { CandlestickController, CandlestickElement } from "chartjs-chart-financial";
import 'chartjs-adapter-date-fns';

Chart.register(CandlestickController, CandlestickElement);

const CandlestickChart = ({ ohlcData, currencyPair, fetchNewData }) => {
  const canvasRef = useRef(null);
  const chartRef = useRef(null);

  useEffect(() => {
    if (!ohlcData || ohlcData.length === 0 || !canvasRef.current) return;

    const ctx = canvasRef.current.getContext("2d");
    if (!ctx) return;

    // Chart configuration
    const chartConfig = {
      type: "candlestick",
      data: {
        datasets: [
          {
            label: currencyPair,
            data: ohlcData.map(d => ({
              x: d.t, // Timestamp
              o: d.o, // Open
              h: d.h, // High
              l: d.l, // Low
              c: d.c, // Close
            })),
            borderColor: {
              up: "#26a69a", // Green for upward
              down: "#ef5350", // Red for downward
            },
            backgroundColor: {
              up: "#26a69a",
              down: "#ef5350",
            },
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        layout: {
          padding: 10,
        },
        scales: {
          x: {
            type: "time",
            time: {
              unit: "minute",
            },
            grid: {
              color: "rgba(255, 255, 255, 0.1)",
            },
            ticks: {
              color: "rgba(255, 255, 255, 0.8)",
            },
          },
          y: {
            grid: {
              color: "rgba(255, 255, 255, 0.1)",
            },
            ticks: {
              color: "rgba(255, 255, 255, 0.8)",
            },
          },
        },
        plugins: {
          tooltip: {
            callbacks: {
              label: context => {
                const point = context.raw;
                return [
                  `Open: ${point.o}`,
                  `High: ${point.h}`,
                  `Low: ${point.l}`,
                  `Close: ${point.c}`,
                ];
              },
            },
          },
        },
      },
    };

    if (chartRef.current) {
      chartRef.current.destroy();
    }

    chartRef.current = new Chart(ctx, chartConfig);

    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [ohlcData, currencyPair]);

  // Polling for data updates
  useEffect(() => {
    const interval = setInterval(() => {
      if (fetchNewData) {
        fetchNewData();
      }
    }, 10000); // Fetch data every 10 seconds

    return () => clearInterval(interval);
  }, [fetchNewData]);

  return (
    <div style={{ backgroundColor: "#131722", padding: "20px", borderRadius: "4px" }}>
      <h3 style={{ color: "white", marginBottom: "20px" }}>{currencyPair} Candlestick Chart</h3>
      <div style={{ height: "400px" }}>
        <canvas ref={canvasRef}></canvas>
      </div>
    </div>
  );
};

export default CandlestickChart;

